@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@400;800&display=swap);
:root {
  --font: 'Epilogue', sans-serif;
  --primary-font-weight: 800;
  --secondary-font-weight: 400;

  --dark-purple-blue-color: #1f3a89;
  --blue-color: #6495ed;
  --white-color: #ffffff;
  --black-color: #333333;
  --danger-color: #ff6c47;
  --success-color: #3ad144;

  --card-shadow-03dp: 0px 3px 4px rgba(0, 0, 0, 0.14),
    0px 3px 3px rgba(0, 0, 0, 0.12), 0px 1px 8px rgba(0, 0, 0, 0.2);
  --card-shadow-04dp: 0px 4px 5px rgba(0, 0, 0, 0.14),
    0px 1px 10px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.2);
  --card-shadow-08dp: 0px 8px 10px rgba(0, 0, 0, 0.14),
    0px 3px 14px rgba(0, 0, 0, 0.12), 0px 5px 5px rgba(0, 0, 0, 0.2);
}

html {
  font-size: 100%;
}

* {
  font-family: 'Epilogue', sans-serif !important;
  font-family: var(--font) !important;
  font-weight: 400;
  font-weight: var(--secondary-font-weight);
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 800;
  font-weight: var(--primary-font-weight);
  line-height: 1.3;
}

h1 {
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small {
  font-size: 0.8rem;
}

.logo {
  max-width: 15rem;
  height: auto;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: var(--dark-purple-blue-color);
  padding: 1rem 0;
  box-shadow: var(--card-shadow-04dp);
  flex-wrap: wrap;
}

.header > * {
  flex-basis: 30% !important;
}

.header-navbar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  list-style: none;
}

.header-cart-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.header-navbar li a {
  text-decoration: none;
  color: var(--white-color);
  font-weight: var(--primary-font-weight);
  text-transform: uppercase;
  font-size: 1.2rem;
  padding: 0 0.5rem;
}

.header-cart-container .cart > * {
  width: 1.8rem;
  height: auto;
  color: var(--white-color);
}

.header-navbar .selected,
.header-navbar a:hover,
.cart.selected > *,
.cart:hover > * {
  color: var(--blue-color);
}

.header-cart-container small {
  color: var(--white-color);
}

@media (max-width: 767px) {
  .header > * {
    flex-basis: auto !important;
  }
  .logo {
    flex-basis: 100%;
  }
}

footer {
  background-color: var(--dark-purple-blue-color);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem 1rem;
  flex-wrap: wrap;
}

.footer-section h5 {
  color: var(--blue-color);
  font-weight: var(--secondary-font-weight);
  text-transform: lowercase;
  font-size: 2rem;
}

.footer-section li a {
  color: var(--white-color);
  text-decoration: none;
  line-height: 1.3;
}

.footer-section li a:hover {
  color: var(--blue-color);
}

.cart-notifier,
.cart-notifier div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: var(--blue-color);
  padding: 0.3rem 0;
  position: sticky;
  top: 0;
}

.cart-notifier * {
  margin-right: 0.5rem;
  color: white;
}

.close-notification {
  cursor: pointer;
}

.close-notification:hover * {
  color: var(--danger-color) !important;
}

.product-card img {
  width: 100%;
  height: auto;
}

.product-card {
  margin: 1rem;
  background-color: var(--dark-purple-blue-color);
  box-shadow: var(--card-shadow-03dp);
}

.product-card:hover {
  box-shadow: var(--card-shadow-08dp);
}

.product-card * {
  color: var(--white-color);
}

.product-infos {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 0.5rem 0.5rem;
}

.product-cart-inline,
.product-card img {
  cursor: pointer;
}

.product-cart-inline:hover > * {
  color: var(--success-color);
}

.product-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.product-list > * {
  flex-basis: 18rem;
}

section h3 {
  color: var(--blue-color);
}

.push-link {
  color: var(--blue-color);
  font-size: 1.5rem;
  text-decoration: none;
}

.push-link:hover {
  color: var(--success-color);
}

.home-section {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.banner {
  margin: 1rem auto;
}

.banner img {
  max-width: 100%;
  height: auto;
}

.home-section > h3,
.home-section .push-link {
  margin-left: 2rem;
  margin-bottom: 1rem;
}

.shop-section h3 {
  margin-top: 1rem;
  margin-left: 3rem;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
}

.product-detailed {
  margin: 1rem 0;
  display: flex;
  justify-content: center;

  flex-direction: column;
  color: var(--blue-color);
}

.product-detailed .product-infos {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  padding: 0.5rem 0;
}

.product-detailed img {
  max-width: 100%;
  width: 100%;
  height: 15rem !important;
}

.product-add-to-cart {
  background-color: var(--blue-color);
  color: var(--white-color);
  padding: 0.5rem 2rem;
  border-radius: 0.3rem;
  font-size: 1.3rem;
  box-shadow: var(--card-shadow-03dp);
  margin: 1rem;
  margin-left: 0;
}

.product-add-to-cart:hover {
  background-color: var(--success-color);
}

.product-detailed label {
  color: var(--blue-color);
  text-transform: uppercase;
}

.product-cart-quantity {
  border: none;
  outline: none;
  width: 3rem;
  font-size: 1.1rem;
  color: var(--blue-color);
  border-bottom: 0.2rem solid var(--blue-color);
}

.product-details-section {
  margin: 1rem 3rem;
}

.support-section {
  margin: 1rem 0 1rem 1rem;
}

.support-section p {
  color: var(--black-color);
  line-height: 1.2;
}

.cart-item,
.cart-item-label {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 1rem;
}

.cart-item > *,
.cart-item-label > * {
  flex-basis: 10%;
  margin-right: 0.5rem;
}

.cart-item img,
.cart-item-label-image {
  flex-basis: 10%;
  height: auto;
}

.cart-item {
  background-color: var(--dark-purple-blue-color);
  padding: 0.5rem 1rem;
  color: var(--white-color);
  border-radius: 0.5rem;
}

.cart-item-actions {
  display: flex;
  align-items: center;
}

.cart-item-action {
  margin-right: 0.5rem;
  font-size: 1.4rem;
  font-weight: var(--primary-font-weight);
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.cart-item-action:hover {
  color: var(--success-color);
}

.cart-item-label {
  font-size: 1.2rem;
  font-weight: var(--primary-font-weight);
  color: var(--blue-color);
  text-transform: uppercase;
}

@media (max-width: 767px) {
  .cart-item,
  .cart-item-label {
    margin: 0.2rem;
    font-size: 0.8rem;
  }
}

.shopping-cart-section {
  margin: 1rem 0 1rem 1rem;
}

.cart-summary-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 1rem;
}

.cart-summary {
  font-size: 1.3rem;
  color: var(--blue-color);
  font-weight: var(--primary-font-weight);
  text-transform: uppercase;
}

.clear-cart-btn {
  background: none;
  outline: none;
  border: none;
  color: var(--danger-color);
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-weight: var(--primary-font-weight);
  cursor: pointer;
  border: 0.1rem solid var(--white-color);
}

.clear-cart-btn:hover {
  border-color: var(--danger-color);
}

.confirm-payment-button {
  width: 99%;
  color: var(--white-color);
  border-radius: 0.5rem;
  background-color: var(--blue-color) !important;
  padding: 1rem;
  margin: 0.5rem 0;
  box-shadow: var(--card-shadow-03dp);
}

.confirm-payment-button:hover {
  background-color: var(--success-color) !important;
}

@media (max-width: 767px) {
  .cart-summary-container {
    margin: 1rem 0 1rem 0;
  }
  .shopping-cart-section {
    margin: 1rem;
  }
}

